@use "../work/media_queries";
@use "media";

.certificates {
  &__title {
    margin-bottom: 7rem;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }

  .certificates__link {
    box-shadow: 0 0.3rem 1rem rgba(192, 192, 192, 0.2);
  }
}
